.Stories[data-v-2a16c8c9] {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Black Fill */
.backdrop[data-v-2a16c8c9] {
    position: absolute;
    top: 0;
    left: 0;
    background: #000;
    width: 100%;
    height: 100%;
    z-index: 10;
}

/* Close button */
.close-stories[data-v-2a16c8c9] {
    position: absolute;
    top: 0;
    right: .5rem;
    color: #fff;
    background: transparent;
    border: none;
    font-size: 2.4rem;
    z-index: 99;
}
a.close-stories[data-v-2a16c8c9] {
    text-decoration: none;
}

/* StoryViewports wrapper.
Displays 4 small + 1 large(current) */
.story-viewports[data-v-2a16c8c9] {
    top: 5vh;
    position: absolute;
}
.story-viewports>li[data-v-2a16c8c9] {
    position: absolute;
    display: flex;
    z-index: 11;
    left: 50vw;
    opacity: 0;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.story-viewports>li.l2[data-v-2a16c8c9],
.story-viewports>li.l1[data-v-2a16c8c9],
.story-viewports>li.r1[data-v-2a16c8c9],
.story-viewports>li.r2[data-v-2a16c8c9],
.story-viewports>li.current[data-v-2a16c8c9] {
    opacity: 1;
}
.story-viewports>li.l3[data-v-2a16c8c9] {
    -webkit-transform: translateX(-270%) scale(.5);
            transform: translateX(-270%) scale(.5);
}
.story-viewports>li.l2[data-v-2a16c8c9] {
    -webkit-transform: translateX(-205%) scale(.5);
            transform: translateX(-205%) scale(.5);
}
.story-viewports>li.l1[data-v-2a16c8c9] {
    -webkit-transform: translateX(-140%) scale(.5);
            transform: translateX(-140%) scale(.5);
}
.story-viewports>li.current[data-v-2a16c8c9] {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.story-viewports>li.r1[data-v-2a16c8c9] {
    -webkit-transform: translateX(40%) scale(.5);
            transform: translateX(40%) scale(.5);
}
.story-viewports>li.r2[data-v-2a16c8c9] {
    -webkit-transform: translateX(105%) scale(.5);
            transform: translateX(105%) scale(.5);
}
.story-viewports>li.r3[data-v-2a16c8c9] {
    -webkit-transform: translateX(170%) scale(.5);
            transform: translateX(170%) scale(.5);
}
.btn-prev[data-v-2a16c8c9],
.btn-next[data-v-2a16c8c9] {
    position: absolute;
    height: 1.5rem;
    width: 1.5rem;
    top: 50vh;
    left: 50vw;
    margin-left: -30vh;
    padding: 0;
    background: rgba(255, 255, 255, .5);
    opacity: .5;
    border: none;
    border-radius: 2rem;
    text-align: center;
    z-index: 90;
    cursor: pointer;
}
.btn-next[data-v-2a16c8c9] {
    margin-left: 27vh;
}